var toDestroy = mount(r(App)
, app);

if (module.hot) {
  module.hot.accept();
  module.hot.dispose(() => {
    // Before restarting the app, we create a new root element and dispose the old one
    toDestroy.destroy();
    var del = document.getElementById('app');
    del.parentNode.replaceChild(del.cloneNode(false), del);
  });
}
